body {
    background-color: #272822;
}

.Wrapper {
    display: -ms-flexbox;
    display: flex; 
    -ms-flex-direction: row; 
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-line-pack: center;
        align-content: center;
    margin: 200px auto;
    /* background-color: white; */
}

.Welcome {
    width: 50%;
    word-wrap: break-word;
}

.mainH1 {
    color: #79D6FF;
    font-family: 'Orbitron', sans-serif;
    font-size: 30px;
}

.welcome {
    color: #F92672;
    font-size: 20px;
 }

 .intro {
    color: #A6E22E;
    font-size: 20px;
    font-family: 'Orbitron', sans-serif;
 }

 @media(max-width: 600px) {
    .Wrapper {
        display: block !important;
    }

    .Welcome {
        width: 100%;
        margin: auto 20px;
    }

    .intro {
        margin: auto 20px;
    }
 }


